/* Style for the form container */
.login-form-container {
  max-width: 500px;
  margin: 190px auto 30px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9ec;
}
  
/* Style for form labels */
.login-form-label {
  display: block;
  margin-bottom: 5px;
  margin-right: 82%;
}

/* Style for form inputs */
.login-form-input {
  width: 94%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Style for the submit button */
.login-form-button {
  display: block;
  /* width: 15%; */
  padding: 10px 15px;
  background-color: #557ca5;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 43.5%;
}

/* Style for error message */
.login-form-error {
  color: red;
}
